@use 'empty-state.mixin' as mixins;
@use '../../assets/scss/elements/typography';
@use '../../assets/scss/elements/accesibility';

:host {
  display: block;
  padding: var(--ath-spacing-empty-state-padding-y) var(--ath-spacing-empty-state-padding-x);

  .ath-empty-state {
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
    align-items: center;
    gap: var(--ath-spacing-empty-state-row-gap-md);
    flex-shrink: 0;

    &--image {
      @include mixins.images;
    }

    &--loading {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 8px;
      &-gif {
        width: 148px;
        height: 120px;
      }
    }

    &-info {
      display: flex;
      flex-direction: column;
      text-align: center;

      .heading,
      .description {
        align-self: stretch;
        color: var(--ath-color-fg-default);
        font-style: normal;
      }

      .heading {
        margin: 0;
      }

      .description {
        font-family: var(--ath-font-family-body);
        font-size: var(--ath-font-size-body-lg);
        font-weight: var(--ath-font-weight-body-regular);
        line-height: var(--ath-font-line-height-body);
      }
    }
  }
}
